* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Open Sans", sans-serif;
  background: #18191a;
}

.show {
  width: 40%;
  margin: 35px auto;
  border-radius: 10px;
  background: #242526;
  padding: 20px 5px;
}
.show_header {
  display: flex;
  align-items: center;
}
.show_header_img {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  overflow: hidden;
}
.show_header_img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.show_header_name {
  margin-left: 12px;
  font-weight: 600;
  color: #e4e6eb;
}
.date {
  color: silver;
  font-weight: normal;
  font-size: 10px;
}

.show_body {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.show_body_text {
  width: 100%;
  margin: 10px 10px;
  overflow: hidden;
  color: #e4e6eb;
  font-size: 15px;
}
.show_body_img {
  margin-bottom: 10px;
}
.show_body_img img {
  width: 100%;
  height: 100%;
}

.show_reactions {
  border-top: 0.2px solid grey;
  border-bottom: 0.2px solid grey;
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
  margin-top: 10px;
  cursor: pointer;
}

.reactions {
  display: flex;
  align-items: center;
}
.reactions-text {
  display: inline-block;
  margin-left: 5px;
  color: #e4e6eb;
}
.icons {
  color: #e4e6eb;
  margin: 5px;
}
.lite {
  color: #e4e6eb;
  margin: 5px;
}
.dark {
  color: #4080ff;
  margin: 5px;
}
.create {
  width: 100%;
  padding: 15px;
}
.create-first {
  display: flex;
  width: 100%;
}
.create-first-img {
  width: 12%;
}
.create-first-img span {
  display: inline-block;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  overflow: hidden;
  margin-top: 8px;
}
.create-first-img span img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.create-input {
  width: 88%;
}
.create-first-input {
  width: 100%;
  padding: 15px 20px;
  border: none;
  background: #3a3b3c;
  border-radius: 30px;
  outline: none;
  color: #e4e6eb;
  font-size: 15px;
}
.create-first-input::placeholder {
  color: #8a8d91;
}
.ul {
  padding: 15px 20px;
  margin-top: 20px;
  margin-left: 5px;
  border: none;
  background: #3a3b3c;
  border-radius: 10px;
  outline: none;
  font-size: 14px;
}
